import { Controller } from '@ali/4ever-cangjie';
import { isHighlight } from '../actions';
import { setHighlight } from '../commands/setHighlightMark';

export default function onAction(action, controller: Controller, next) {
  if (isHighlight(action)) {
    const { highlight } = action.payload;
    setHighlight(controller, highlight);
  }
  return next();
}
